@use 'base/variables' as v;
@use 'base/mixins' as m;

.sobre-festival {
    @include m.contenedor;
    @include m.tablet {
        @include m.grid(2, 5rem);
    }
}

.contenido-festival {
    margin-top: 2rem;
    h2 {
        margin-bottom: 2rem;
    }
    .fecha {
        color: v.$verde;
        font-weight: 700;
    }
    
}